import { Component, Prop, h } from '@stencil/core';
import { SPINNER_SIZES } from './exports';

@Component({
  tag: 'mdx-spinner',
  styleUrl: 'spinner.scss',
  shadow: true,
})
export class Spinner {
  @Prop()
  size: (typeof SPINNER_SIZES)[keyof typeof SPINNER_SIZES] = SPINNER_SIZES.MEDIUM;

  render() {
    return (
      <svg class="spinner" viewBox="25 25 50 50" stroke-width="5">
        <circle class="spinner__circle" cx="50" cy="50" r="20" />
      </svg>
    );
  }
}
