:host .flyout {
  box-sizing: border-box;
  list-style: none;
  background-color: var(--mdx-comp-flyout-background-color-default);
  border-color: var(--mdx-comp-flyout-border-color-default);
  letter-spacing: var(--mdx-comp-flyout-font-default-letter-spacing);
  text-decoration: var(--mdx-comp-flyout-font-default-text-decoration);
  font: var(--mdx-comp-flyout-font-default);
  padding-top: var(--mdx-comp-flyout-padding-vertical-default);
  padding-bottom: var(--mdx-comp-flyout-padding-vertical-default);
  border-radius: var(--mdx-comp-flyout-border-radius-default);
  border-width: var(--mdx-comp-flyout-border-width-default);
  box-shadow: var(--mdx-comp-flyout-box-shadow-default);
}
