import { Component, Element, Listen, h } from '@stencil/core';

@Component({
  tag: 'mdx-flyout',
  styleUrl: 'flyout.scss',
  shadow: true,
})
export class Flyout {
  @Element()
  host: HTMLMdxFlyoutElement;

  @Listen('keydown', { capture: true, target: 'window' })
  handleKeyDown(event: KeyboardEvent) {
    if (!(event.key === 'ArrowDown' || event.key === 'ArrowUp')) {
      return;
    }

    const items = this.items;
    let currentIndex = items.findIndex((item) => item === document.activeElement);

    if (currentIndex === -1) {
      return;
    }

    if (event.key === 'ArrowDown') {
      currentIndex = (currentIndex + 1) % items.length;
    } else if (event.key === 'ArrowUp') {
      currentIndex = (currentIndex - 1 + items.length) % items.length;
    }

    items[currentIndex].focusItem();
    event.preventDefault();
  }

  private get items() {
    return Array.from(this.host.querySelectorAll('mdx-flyout-item'));
  }

  componentDidLoad() {
    const items = this.items;
    if (items.length > 0) {
      items[0].selected = true;
    }
  }

  render() {
    return (
      <div class="flyout">
        <slot></slot>
      </div>
    );
  }
}
