import { AttachInternals, Component, Element, h, Prop } from '@stencil/core';
import { isExternalUrl } from '../../utils';
import { BUTTON_AS, BUTTON_SIZES, BUTTON_TYPE, BUTTON_VARIANTS } from './exports';

/**
 * @slot - For Label and Icon
 */
@Component({
  tag: 'mdx-button',
  styleUrl: 'button.scss',
  shadow: true,
  formAssociated: true,
})
export class Button {
  @Element()
  host: HTMLMdxButtonElement;

  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  variant: (typeof BUTTON_VARIANTS)[keyof typeof BUTTON_VARIANTS] = BUTTON_VARIANTS.PRIMARY;

  @Prop({ reflect: true })
  size: (typeof BUTTON_SIZES)[keyof typeof BUTTON_SIZES] = BUTTON_SIZES.MD;

  @Prop({ reflect: true })
  type: (typeof BUTTON_TYPE)[keyof typeof BUTTON_TYPE] = BUTTON_TYPE.BUTTON;

  @Prop({ reflect: true })
  as: (typeof BUTTON_AS)[keyof typeof BUTTON_AS] = BUTTON_AS.BUTTON;

  @Prop()
  href: string;

  @Prop()
  fullWidth: boolean;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  @AttachInternals()
  internals: ElementInternals;

  componentDidLoad() {
    if (this.type === BUTTON_TYPE.SUBMIT) {
      const button = this.host.shadowRoot?.querySelector('button');
      button?.addEventListener('click', () => {
        if (this.internals.form?.checkValidity()) {
          this.internals.form?.submit();
        } else {
          return;
        }
      });
    }
  }

  private isExternalURL() {
    return isExternalUrl(this.href);
  }

  render() {
    if (this.as === BUTTON_AS.BUTTON) {
      return (
        <button class="button" type={this.type} disabled={this.disabled}>
          <slot></slot>
        </button>
      );
    }

    return (
      <a class="button" href={this.href} target={this.target} rel={this.isExternalURL() ? 'noreferrer' : undefined}>
        <slot></slot>
      </a>
    );
  }
}
