import { AttachInternals, Component, Element, Event, EventEmitter, h, Host, Listen, Prop, State } from '@stencil/core';
import { parseAttribute } from '../../utils';
import { ICON_SIZES } from '../icons/exports';
import { SPINNER_SIZES } from '../spinner/exports';
import { INPUT_TYPES } from './export';

export type SuggestionList = { label: string }[];

/**
 * @slot icon - Slot for the icon
 */
@Component({
  tag: 'mdx-input',
  styleUrl: 'input.scss',
  shadow: true,
  formAssociated: true,
})
export class Input {
  @Event()
  inputChange: EventEmitter<{ input: string }>;

  @Element()
  host: HTMLMdxInputElement;

  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  required = false;

  @Prop()
  label!: string;

  @Prop()
  value = '';

  @Prop()
  placeholder = '';

  @Prop()
  hint?: string;

  @Prop()
  errorMessage: string = '';

  @State()
  validationMessage: string;

  @State()
  isValid: boolean;

  @Prop()
  counter?: number;

  @Prop()
  loading = false;

  @Prop()
  name: string;

  @Prop()
  suggestionList?: SuggestionList;

  @Prop({ reflect: true })
  selectedItem: HTMLMdxFlyoutItemElement['item'];

  @Prop({ reflect: true })
  type: (typeof INPUT_TYPES)[keyof typeof INPUT_TYPES] = INPUT_TYPES.TEXT;

  @Prop()
  passwordInputIconAriaLabel?: string;

  @Prop()
  inputPattern: string;

  @State()
  focused = false;

  @State()
  keyboardEvent = false;

  @State()
  matchedInput: { label: string }[] = [];

  @State()
  isPasswordVisible = false;

  @State()
  showIconContainer = false;

  @AttachInternals()
  internals: ElementInternals;

  @Listen('click', { capture: true, target: 'window' })
  handleClick() {
    this.keyboardEvent = false;
  }

  @Listen('keydown', { capture: true, target: 'window' })
  handleKeyDown(event: KeyboardEvent) {
    if (event.key === 'Tab') {
      this.keyboardEvent = true;
    }
  }

  private handleInputChange(e: Event) {
    const inputElement = e.currentTarget as HTMLInputElement;
    const v = inputElement.value;
    if (this.counter !== undefined && (v.length || 0) > this.counter) {
      this.value = v.slice(0, Math.min(v.length, this.counter));
      (e.currentTarget as HTMLInputElement).value = this.value;
    } else {
      this.value = v;
    }

    const matchedItems = this.suggestionList?.filter((item) => item.label.toLowerCase().includes(v.toLowerCase()));
    this.matchedInput = matchedItems || [];

    this.inputValidation(e);
    this.internals.setFormValue(this.value);
    this.inputChange.emit({ input: this.value });
  }

  private inputValidation(e: Event) {
    const inputElement = e.currentTarget as HTMLInputElement;
    this.isValid = inputElement.checkValidity();
    this.validationMessage = !this.isValid ? this.errorMessage || inputElement.validationMessage : '';
    this.internals.setValidity({ customError: !this.isValid }, this.validationMessage);
  }

  private select(item: string) {
    if (this.selectedItem === item) {
      return false;
    }
    this.selectedItem = item;

    return (this.value = this.selectedItem);
  }

  private handlePasswordVisibilityChange() {
    if (this.disabled) {
      return;
    }
    this.isPasswordVisible = !this.isPasswordVisible;
  }

  componentDidRender() {
    this.showIconContainer =
      this.loading || this.type === INPUT_TYPES.PASSWORD || this.host.querySelector('[slot="icon"]') !== null;

    if (typeof this.errorMessage === 'string' && this.errorMessage !== '') {
      this.internals.setValidity({ customError: true }, this.errorMessage);
    } else if (this.errorMessage === '') {
      this.host.removeAttribute('error-message');
    }
  }

  connectedCallback() {
    this.suggestionList = parseAttribute(this.host, 'suggestion-list', this.suggestionList);
  }

  render() {
    return (
      <Host
        onItemClick={({ detail: { item } }) => {
          this.select(item);
        }}
      >
        <label class={`input ${this.focused && 'focused'}`} htmlFor={this.name}>
          <div class="input__label">
            {this.label}
            {this.required && <span class="asterisk">*</span>}
          </div>
          <div class="input__wrapper">
            <input
              class={`input__field ${
                this.type === INPUT_TYPES.PASSWORD
                  ? this.isPasswordVisible
                    ? 'input__field-value--visible'
                    : 'input__field-value--hidden'
                  : ''
              }`}
              type={this.type === INPUT_TYPES.PASSWORD && this.isPasswordVisible ? INPUT_TYPES.TEXT : this.type}
              onFocus={() => this.keyboardEvent && (this.focused = true)}
              onBlur={() => (this.focused = false)}
              onInput={(e) => this.handleInputChange(e)}
              required={this.required}
              placeholder={this.placeholder}
              disabled={this.disabled}
              value={this.value}
              pattern={this.inputPattern}
              name={this.name}
            />
            {this.showIconContainer && (
              <div class="input__icon-container">
                {this.loading && <mdx-spinner class="input__loading-spinner" size={SPINNER_SIZES.MEDIUM} />}
                {this.type === INPUT_TYPES.PASSWORD && (
                  <button
                    class="input__password-icon-button"
                    onClick={() => this.handlePasswordVisibilityChange()}
                    aria-label={this.passwordInputIconAriaLabel}
                  >
                    {!this.isPasswordVisible ? (
                      <mdx-icon-eye size={ICON_SIZES.SM} />
                    ) : (
                      <mdx-icon-eye-off size={ICON_SIZES.SM} />
                    )}
                  </button>
                )}
                <slot name="icon" />
              </div>
            )}
          </div>
          <div class="input__information-wrapper">
            <div>
              {typeof this.errorMessage === 'string' && this.errorMessage !== '' ? (
                <mdx-error-hint class="input__error-message" text={this.errorMessage} />
              ) : this.isValid === false ? (
                <mdx-error-hint class="input__error-message" text={this.validationMessage} />
              ) : (
                <span class="input__hint">{this.hint}</span>
              )}
            </div>
            {this.counter && this.counter > 0 && (
              <span class="input__counter">
                {this.value.length} / {this.counter}
              </span>
            )}
            {this.value && this.suggestionList && this.suggestionList.length > 0 && this.type !== INPUT_TYPES.PASSWORD && (
              <div class="input__flyout">
                {this.matchedInput.length > 0 && (
                  <mdx-flyout>
                    {this.matchedInput.map((element) => (
                      <mdx-flyout-item item={element.label} label={element.label}></mdx-flyout-item>
                    ))}
                  </mdx-flyout>
                )}
              </div>
            )}
          </div>
        </label>
      </Host>
    );
  }
}
