:host .flyout-item {
  box-sizing: border-box;
  padding-top: var(--mdx-comp-flyout-item-padding-vertical-default);
  padding-bottom: var(--mdx-comp-flyout-item-padding-vertical-default);
  padding-left: var(--mdx-comp-flyout-item-padding-horizontal-default);
  padding-right: var(--mdx-comp-flyout-item-padding-horizontal-default);
  background-color: var(--mdx-comp-flyout-item-background-color-default);
  color: var(--mdx-comp-flyout-item-font-color-default);
  border-width: var(--mdx-comp-flyout-item-border-width-default);
  width: 100%;
  height: 100%;
  cursor: pointer;
  border: none;
  outline: none;
  text-align: left;
  font: var(--mdx-comp-flyout-font-default);

  &:hover,
  &:focus-within {
    background-color: var(--mdx-comp-flyout-item-background-color-hover);
    color: var(--mdx-comp-flyout-item-font-color-hover);
    border-color: var(--mdx-comp-flyout-item-border-color-selected);
  }
}

:host([selected]) .flyout-item {
  border-style: solid;
  border-width: var(--mdx-comp-flyout-item-border-width-selected-top) var(--mdx-comp-flyout-item-border-width-selected-right)
    var(--mdx-comp-flyout-item-border-width-selected-bottom) var(--mdx-comp-flyout-item-border-width-selected-left);
  border-color: var(--mdx-comp-flyout-item-border-color-selected);
  background-color: var(--mdx-comp-flyout-item-background-color-selected);
  color: var(--mdx-comp-flyout-item-font-color-selected);
}
