:host .input {
  position: relative;
  box-sizing: border-box;
  color: var(--mdx-comp-inputfield-label-color-default);
  display: flex;
  flex-direction: column;
  font: var(--mdx-comp-inputfield-font-label);
  letter-spacing: var(--mdx-comp-inputfield-font-label-letter-spacing);
  line-height: var(--mdx-comp-inputfield-font-label-line-height);
  margin-bottom: var(--mdx-comp-inputfield-gap-content-below);
  text-decoration: var(--mdx-comp-inputfield-font-label-text-decoration);
  text-transform: var(--mdx-comp-inputfield-font-label-text-case);

  @media screen and (max-width: 768px) {
    width: var(--mdx-comp-inputfield-container-sizing-mobile-width);
  }

  &__wrapper {
    box-shadow: var(--mdx-comp-inputfield-box-shadow-default);
    font: var(--mdx-comp-inputfield-font-default);
    text-decoration: var(--mdx-comp-inputfield-font-default-text-decoration);
    text-transform: var(--mdx-comp-inputfield-font-default-text-case);
    display: flex;
    align-items: center;
    margin: var(--mdx-comp-inputfield-gap-label-inputfield) 0;

    &:has(.input__icon-container) .input__field:not([type='date']) {
      padding-right: calc(
        var(--mdx-comp-inputfield-sizing-icon) + var(--mdx-comp-inputfield-padding-horizontal-default) +
          var(--mdx-comp-inputfield-gap-icon-loading-icon)
      );
    }

    &:has(.input__icon-container):has(.input__loading-spinner) .input__field {
      padding-right: calc(
        2 * (var(--mdx-comp-inputfield-sizing-icon) + var(--mdx-comp-inputfield-padding-horizontal-default))
      );
    }
  }

  &__field {
    font: var(--mdx-comp-inputfield-font-default);
    text-decoration: var(--mdx-comp-inputfield-font-default-text-decoration);
    text-transform: var(--mdx-comp-inputfield-font-default-text-case);
    border: none;
    flex-grow: 1;
    outline: none;
    padding: var(--mdx-comp-inputfield-padding-vertical-default) var(--mdx-comp-inputfield-padding-horizontal-default);
    border-color: var(--mdx-comp-inputfield-border-color-default);
    border-radius: var(--mdx-sys-border-radius-default);
    border-width: var(--mdx-comp-inputfield-border-width-default);
    background-color: var(--mdx-comp-inputfield-background-color-default);
    color: var(--mdx-comp-inputfield-input-color-default);
    border-style: solid;

    &:focus {
      border-color: var(--mdx-comp-inputfield-border-color-focus);
    }

    &::placeholder {
      color: var(--mdx-comp-inputfield-placeholder-color-default);
    }

    &:invalid {
      border-color: var(--mdx-comp-inputfield-border-color-error);
      box-shadow: var(--mdx-comp-inputfield-box-shadow-error);
      border-width: var(--mdx-comp-inputfield-border-width-default);
      border-style: solid;
    }
  }

  &__icon-container {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: var(--mdx-comp-inputfield-padding-horizontal-default);
    gap: var(--mdx-comp-inputfield-gap-icon-loading-icon);
  }

  &__information-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font: var(--mdx-comp-inputfield-font-supporting);
    position: relative;
  }

  &__hint {
    color: var(--mdx-comp-inputfield-hint-counter-color-default);
  }

  &__flyout {
    position: absolute;
    width: 100%;
  }

  &__password-visibility {
    color: var(--mdx-comp-inputfield-border-color-default);
  }

  &.focused {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    color: var(--mdx-comp-inputfield-label-color-focus);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 2px;
  }

  ::slotted([slot='icon']) {
    color: var(--mdx-comp-inputfield-border-color-default);
  }

  &__password-icon-button {
    all: unset;
    color: var(--mdx-comp-inputfield-icon-color-default);

    &:focus-visible {
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      color: var(--mdx-comp-inputfield-label-color-focus);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
      outline-style: solid;
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-offset: 2px;
    }
  }
}

:host([type='password']) .input__field {
  &-value--visible {
    font: var(--mdx-comp-inputfield-font-default);
  }

  &-value--hidden {
    font-family: Verdana, Geneva, sans-serif;
    letter-spacing: 0.125em;
  }

  &::placeholder {
    font-family: var(--mdx-comp-inputfield-font-default-font-family);
    letter-spacing: var(--mdx-comp-inputfield-font-default-letter-spacing);
  }
}

:host([disabled]) .input {
  &__label {
    color: var(--mdx-comp-inputfield-label-color-disabled);
  }

  &__field {
    color: var(--mdx-comp-inputfield-placeholder-color-disabled);
    background-color: var(--mdx-comp-inputfield-background-color-disabled);
    border-color: var(--mdx-comp-inputfield-border-color-disabled);
  }

  &__field::placeholder {
    color: var(--mdx-comp-inputfield-placeholder-color-disabled);
    background-color: var(--mdx-comp-inputfield-background-color-disabled);
  }

  &__wrapper {
    background-color: var(--mdx-comp-inputfield-background-color-disabled);
  }

  ::slotted([slot='icon']) {
    color: var(--mdx-comp-inputfield-border-color-disabled);
  }

  &__password-icon-button {
    color: var(--mdx-comp-inputfield-icon-color-disabled);
  }

  &__hint,
  &__counter {
    color: var(--mdx-comp-inputfield-hint-counter-color-disabled);
  }
}

:host([required]) .asterisk {
  margin-left: var(--mdx-comp-inputfield-gap-label-asterisk);
}

:host([loading]) .input {
  &__loading-spinner {
    display: flex;
    gap: var(--mdx-comp-inputfield-gap-icon-loading-icon);
    height: var(--mdx-comp-inputfield-sizing-icon);
    width: var(--mdx-comp-inputfield-sizing-icon);
  }
}

:host([error-message]) .input {
  &__label {
    color: var(--mdx-comp-inputfield-label-color-error);
  }

  &__field {
    background-color: var(--mdx-comp-inputfield-background-color-error);
    border-color: var(--mdx-comp-inputfield-border-color-error);
    box-shadow: var(--mdx-comp-inputfield-box-shadow-error);
    color: var(--mdx-comp-inputfield-input-color-error);
  }

  &__field::placeholder {
    color: var(--mdx-comp-inputfield-placeholder-color-error);
  }
}

input[type='date']::-webkit-inner-spin-button,
input[type='date']::-webkit-calendar-picker-indicator {
  display: none;
}

@media screen and (max-width: 768px) {
  input[type='date']::-webkit-inner-spin-button,
  input[type='date']::-webkit-calendar-picker-indicator {
    display: block;
  }
  ::slotted([slot='icon']) {
    display: none;
  }
}
