:host .spinner {
  box-sizing: border-box;
  vertical-align: middle;
  transform-origin: center;
  animation: rotate 2s linear infinite;

  &__circle {
    fill: none;
    stroke: var(--mdx-comp-loadingspinner-color-default);
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    stroke-linecap: round;
    animation: stretch 1.5s ease-in-out infinite;
  }
}

:host([size='small']) .spinner {
  height: var(--mdx-comp-loadingspinner-small-sizing-default);
  width: var(--mdx-comp-loadingspinner-small-sizing-default);
}

:host([size='medium']) .spinner {
  height: var(--mdx-comp-loadingspinner-medium-sizing-default);
  width: var(--mdx-comp-loadingspinner-medium-sizing-default);
}

:host([size='large']) .spinner {
  height: var(--mdx-comp-loadingspinner-large-sizing-default);
  width: var(--mdx-comp-loadingspinner-large-sizing-default);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes stretch {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dashoffset: -124px;
  }
}
