import { Component, h, Prop } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

@Component({
  tag: 'mdx-error-hint',
  styleUrl: 'error-hint.scss',
  shadow: true,
})
export class ErrorHint {
  @Prop()
  text: string;

  render() {
    return (
      <div class="error-hint">
        <span class="error-hint__icon">
          <mdx-icon-alert-circle size={ICON_SIZES.XS} />
        </span>
        {this.text}
      </div>
    );
  }
}
