import { Component, Element, Event, EventEmitter, Listen, Method, Prop, h } from '@stencil/core';

@Component({
  tag: 'mdx-flyout-item',
  styleUrl: 'flyout-item.scss',
  shadow: true,
})
export class FlyoutItem {
  @Event()
  itemClick: EventEmitter<{ item: string; selected: boolean; label: string }>;

  @Prop()
  label!: string;

  @Prop({ reflect: true })
  selected = false;

  @Prop({ reflect: true })
  item!: string;

  @Element()
  host: HTMLMdxFlyoutItemElement;

  @Method()
  // eslint-disable-next-line @stencil-community/async-methods
  async focusItem() {
    this.buttonEl?.focus();
  }

  @Listen('click', { target: 'window' })
  handleOutsideClick(event: MouseEvent) {
    if (this.selected && !this.host.contains(event.target as Node)) {
      this.selected = false;
    }
  }

  private buttonEl?: HTMLButtonElement;

  render() {
    return (
      <button
        ref={(el) => (this.buttonEl = el)}
        aria-selected={this.selected}
        class="flyout-item"
        onClick={() => {
          this.selected = !this.selected;
          this.itemClick.emit({ item: this.item, selected: this.selected, label: this.label });
        }}
      >
        {this.label}
      </button>
    );
  }
}
